import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import { FooterFormulario, PageApp } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { FORMATO_FECHA_BACKEND, RUTAS_APP } from '../../constants'
import { plata_fmt } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'
import { MovimientoTransportista } from '../../services/api'
import './GananciaTransportista.css'

const numeral = require('numeral')
const moment = require('moment-timezone')

export const GananciaTransportista: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [MovimientosPendienteFacturacion, setMovimientosPendienteFacturacion] = useState(
    [] as MovimientoTransportista[],
  )
  const [MovimientosFacturados, setMovimientosFacturados] = useState([] as MovimientoTransportista[])
  const [MovimientosCobrados, setMovimientosCobrados] = useState([] as MovimientoTransportista[])
  const [Balance, setBalance] = useState({
    facturado: 0,
    pendiente_facturacion: 0,
    cobrado: 0,
    cantidad: 0,
    ganancia_intervalo: 0,
  })
  const [Diario, setDiario] = useState(true)
  const [TiempoConectado, setTiempoConectado] = useState<number | null>()
  const [HorasConectado, setHorasConectado] = useState<number | null>()
  const [MinutosConectado, setMinutosConectado] = useState<number | null>()
  const [FechaSeleccionada, setFechaSeleccionada] = useState(moment().format())
  const [FechaInicio, setFechaInicio] = useState<null | string>(null)
  const [FechaFin, setFechaFin] = useState<null | string>(null)

  const setup = () => {
    setLoading(true)
    obtenerBalance()
    obtenerTiempoConectado()
  }

  React.useEffect(() => {
    if (!!FechaInicio && !!FechaFin) {
      setup()
    }
  }, [FechaInicio, FechaFin, Diario])

  React.useEffect(() => {
    const duracionConetado = moment.duration(TiempoConectado, 'seconds')
    const min = duracionConetado.minutes()
    const hrs = duracionConetado.hours()

    if (!!min) {
      setMinutosConectado(min)
    } else {
      setMinutosConectado(null)
    }

    if (!!hrs) {
      setHorasConectado(hrs)
    } else {
      setHorasConectado(null)
    }
  }, [TiempoConectado])

  React.useEffect(() => {
    if (Diario) {
      setFechaInicio(moment(FechaSeleccionada).subtract(1, 'days').format(FORMATO_FECHA_BACKEND))
      setFechaFin(moment(FechaSeleccionada).add(1, 'days').format(FORMATO_FECHA_BACKEND))
    } else {
      setFechaInicio(moment(FechaSeleccionada).startOf('week').subtract(1, 'days').format(FORMATO_FECHA_BACKEND))
      setFechaFin(moment(FechaSeleccionada).endOf('week').add(1, 'days').format(FORMATO_FECHA_BACKEND))
    }
  }, [FechaSeleccionada])

  const obtenerBalance = () => {
    rootStore.environment.api.getBalanceDetail(20, FechaFin, FechaInicio).then((result) => {
      if (result.kind === 'ok') {
        setBalance(result.data)
        transportistaStore.setBalance(result.data.pendiente_facturacio)
        setLoading(false)
      }
    })
  }

  const obtenerTiempoConectado = () => {
    rootStore.environment.api.getTiempoConectadoTransportista(FechaFin, FechaInicio).then((result) => {
      if (result.kind === 'ok') {
        setTiempoConectado(result.data.tiempo_conectado_total)
      }
    })
  }

  return (
    <PageApp
      preset={'p-driver'}

      subBarraExternaHeader={<SubHeader links={[{ titulo: 'Balance' }]} />}
      sub_titulo={` `}
      mostrar_barra={false}
    >
      <div id={'transportista-balance-container'}>
        {Loading && <IonSpinner name="crescent" className={'loadingSpinner'} />}
        {!Loading && (
          <div className="container">
            <div className="row">
              <div className="col">
                <button
                  onClick={() => setDiario(true)}
                  className={Diario ? 'btn-outline-transportista' : 'btn-outline-gris'}
                >
                  Día
                </button>
              </div>
              <div className="col">
                <button
                  onClick={() => setDiario(false)}
                  className={!Diario ? 'btn-outline-transportista' : 'btn-outline-gris'}
                >
                  Semanal
                </button>
              </div>
            </div>
            <div className="fecha-seleccionada-container">
              <a
                onClick={() => {
                  const currentDate = moment(FechaSeleccionada)
                  if (Diario) {
                    setFechaSeleccionada(currentDate.subtract(1, 'days'))
                  } else {
                    setFechaSeleccionada(currentDate.subtract(1, 'weeks'))
                  }
                }}
              >
                <FIcon.ChevronLeft />
              </a>

              <div>
                {Diario && (
                  <span style={{ fontWeight: 'bolder' }}>
                    {moment(FechaSeleccionada).format('dddd[,]  D [de] MMM')}
                  </span>
                )}
                {!Diario && (
                  <span style={{ fontWeight: 'bolder' }}>
                    Del {moment(FechaSeleccionada).startOf('week').format('D')} al{' '}
                    {moment(FechaSeleccionada).endOf('week').format('D [de] MMM')}
                  </span>
                )}
                <br />
                <span style={{ fontWeight: 'bold', color: 'var(--verde-claro)', fontSize: 25 }}>
                  {plata_fmt(Balance.ganancia_intervalo)}
                </span>
                <br />
                <span style={{ color: 'var(--verde-claro)' }}> Ganancias</span>
              </div>
              <a
                onClick={() => {
                  const currentDate = moment(FechaSeleccionada)
                  if (Diario) {
                    setFechaSeleccionada(currentDate.add(1, 'days'))
                  } else {
                    setFechaSeleccionada(currentDate.add(1, 'weeks'))
                  }
                }}
              >
                <FIcon.ChevronRight />
              </a>
            </div>
            <div className="sumatorias-entrega-tiempo-container">
              <div>
                <strong>{Balance.cantidad}</strong>
                <br />
                <span>Entregas</span>
              </div>
              <div style={{ backgroundColor: 'var(--gris-mas-claro)', width: '100%' }} />
              <div>
                <strong>
                  {!!TiempoConectado ? (
                    <>
                      {HorasConectado && <span>{HorasConectado} h</span>}{' '}
                      {MinutosConectado && <span>{MinutosConectado} min</span>}
                    </>
                  ) : (
                    0
                  )}
                </strong>
                <br />
                <span>Tiempo conectado</span>
              </div>
            </div>

            <div className="monto-total-balance-transportista-container">
              <span>Ganancia total</span>
              <br />
              <span className="monto-total-balance-transportista">
                {plata_fmt(Balance.facturado + Balance.pendiente_facturacion)}
              </span>
            </div>

            <div className="monto-total-balance-transportista-container">
              <span>Cobrado</span>
              <br />
              <span className="monto-total-balance-transportista">{plata_fmt(Balance.cobrado)}</span>
            </div>
            <div className="monto-total-balance-transportista-container">
              <span>Pendiente a cobrar</span>
              <br />
              <span className="monto-total-balance-transportista">
                {plata_fmt(Balance.facturado + Balance.pendiente_facturacion - Balance.cobrado)}
              </span>
            </div>
          </div>
        )}
        {!Loading && (
          <FooterFormulario className="py-4">
            <div className="row ">
              <div className="col">
                <button className="btn-principal-transportista col" onClick={() => history.push(RUTAS_APP.MIS_RUTAS)}>
                  Ver historial de entregas
                </button>
              </div>
            </div>
          </FooterFormulario>
        )}
      </div>
    </PageApp>
  )
})

export default GananciaTransportista
